// db.js
import Dexie from 'dexie';


export const db = new Dexie('rareDiseasesDB');
db.version(1).stores({
    phenotype: `&id, name`,
    disease: `&id, name, *phenotypeIds`,
    note: `++id`,
});

// diseases2Phenotypes: `[diseaseId + phenotypeId], onset, frequency`,

const Phenotype = db.phenotype.defineClass({
    id: Number,
    name: String
});

Phenotype.prototype.save = function () {
    return db.phenotype.put(this);
}

const Disease = db.disease.defineClass({
    id: Number,
    phenotypeIds: [Number],
    description: String,
    ref: String,
    images: [String],
});

Disease.prototype.save = function () {
    return db.disease.put(this);
}

